.bold {
  font-weight: 600;
  text-transform: uppercase;
}
.App {
  position: absolute;
  width:100%;
  height: 100%;

  header {
    position: relative;
    width: 100%;
    text-align: center;
    margin-bottom: 8px;

    > div {
      font-size: 15px;
      width: 324px;
      text-align: center;
      margin: 0 auto;
    }
  }

  footer {
    width: 360px;
    position: relative;
    margin: 0 auto;
    padding: 10px 4px;
    margin-top: 10px;
    
    .score {
      justify-content: right;
      //display: flex;

      div {
        padding: 8px
      }
    }

    .restart {
      //background-color: black;
      display: flex;
      justify-content: center;
    }
    .moves, .high-score{
      color: red;
    }

  }

  .container {
    padding: 6px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    justify-items: center;
    align-items: stretch;
    gap: 0.5rem;
    margin: 0 auto;
    width: 500px;
    height: 400px;
    perspective: 100%;
    max-width: 720px;
  }
}